.button {
  display: block;
  width: 185px;
  padding: 18px 0;
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  text-decoration: none;
  color: black;
  background: white;
  border: 2px solid #2196F3;
  border-radius: 5px;
  transition-duration: 0.1s;

  &:hover {
    background-color: #2196F3;
    color: white;
    transition-duration: 0.1s;
  }

  &:active {
    background-color: #2196F3;
    color: white;
    opacity: 0.3;
  }
}
